<template>
  <div>
    <v-card class="mx-auto card" max-width="250" elevation="10">
      <v-img :src="card.src" height="200px"></v-img>
      <v-card-title>{{card.title}}</v-card-title>
      <v-card-subtitle>{{card.subTitulo}}</v-card-subtitle>

      <v-card-actions>
        <v-btn color="purple" text @click="dialog = true">Explorar</v-btn>
        <v-spacer></v-spacer>
        <v-btn icon @click="show = !show">
          <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
        </v-btn>
      </v-card-actions>

      <v-expand-transition>
        <div v-show="show">
          <v-divider></v-divider>
          <v-card-text>{{card.descricao}}</v-card-text>
        </div>
      </v-expand-transition>
    </v-card>

    <v-row justify="center">
      <v-dialog v-model="dialog" :width=card.width>
        <v-card>
          <v-card-title>
            <span class="headline">{{card.title}}</span>
          </v-card-title>
          <v-card-text>
            <v-carousel>
              <v-carousel-item
                v-for="(item,i) in card.items"
                :key="i"
                :src="item.src"
                reverse-transition="fade-transition"
                transition="fade-transition"
              ></v-carousel-item>
            </v-carousel>
            <v-spacer></v-spacer>
            <br />
            {{card.descricao}}
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green darken-1" text @click="dialog = false">Closed</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
export default {
  props: ["card"],
  data: () => ({
    dialog: false,
    show: false
  })
};
</script>

<style >
.card {
  margin: 10px;
}
</style>